.wrapper {
    position: relative;
    display: flex;
    width: var(--width);
    height: 552px;
    border: 1px solid #333;
    margin-top: 10px;

    // left: 50%;
    // transform: translateX(-50%);
    background: linear-gradient(150deg, rgb(168, 45, 72), rgb(1, 1, 39));
    background-color: #888;
    flex-direction: column;
    overflow: auto;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
}

.wrapper::-webkit-scrollbar {
    width: 0 !important;
    display: none;
}

.playing {
    color: #fff;
    position: relative;
    display: flex;
    align-items: center;
    top: 0;
    left: 3%;
    width: 94%;
    height: 60px;
    border-radius: 15px;
    background: linear-gradient(to right, #696e1e, rgb(65, 65, 119));
    margin: 0 0 20px 0;
    .name-author {
        display: block;
        margin-left: 20px;
        align-items: center;
        .song-name {
            margin: 6px 0 0 0;
            font-size: 18px;
            font-weight: 300;
            line-height: 24px;
        }
        .song-author {
            font-size: 13px;
            font-weight: 400;
        }
    }

    .curr-img {
        display: flex;
        margin-left: 10px;
        border-radius: 5px;
        overflow: hidden;
        width: 40px;
        height: 40px;
    }
}
